import React, { Component } from 'react';
import { BasicHeader, Card, CardContent, Checkbox, Clone, DropDown, FormItem, ImageButton, InputForm, SetProperty, TextButton, TextInput } from 'vhp-component-library';

import './Styles/TrackerEditor.css'
import { Pie, PieChart, ResponsiveContainer, Cell, Legend, Tooltip } from 'recharts';

let CategoryList = [];
let SaleTypeList = [];
let TimeRanList = [];
let CompanyList = [];
let LeadSourceList = [];
let PresentedViaList = [];
let ProgressList = [];

function SetDroplists(settings) {
	let rsets = settings.reporting;
	CategoryList = [];
	SaleTypeList = [];
	TimeRanList = [];
	CompanyList = [];
	LeadSourceList = [];
	PresentedViaList = [];
	ProgressList = [];
	
	for(let ea in rsets.cat){  // Categories
		CategoryList.push({
			text: rsets.cat[ea].descr,
			value: ea
		})
	}

	for(let ea in rsets.source){  // Leads & Sources
		LeadSourceList.push({
			text: rsets.source[ea],
			value: ea
		})
	}
	
	for(let ea in rsets.comp){  // Companies
		CompanyList.push({
			text: rsets.comp[ea],
			value: ea
		})
	}
	
	for(let ea in rsets.time){  // Times
		TimeRanList.push({
			text: rsets.time[ea],
			value: ea
		})
	}

	for(let ea in rsets.prstvia){  // Presentation Types
		PresentedViaList.push({
			text: rsets.prstvia[ea],
			value: ea
		})
	}

	for(let ea in rsets.saletype){  // Sale Types
		SaleTypeList.push({
			text: rsets.saletype[ea],
			value: ea
		})
	}

	for(let ea in rsets.progress){  // Sale Types
		ProgressList.push({
			text: rsets.progress[ea],
			value: ea
		})
	}

	let DropDownKeys = {
		source:LeadSourceList,
		time:TimeRanList,
		prstvia:PresentedViaList,
		cat:CategoryList,
		comp:CompanyList,
		lead:LeadSourceList,
		saletype:SaleTypeList,
		progress:ProgressList
	}

	return DropDownKeys
}
let CheckboxKeys = {
	bookprc:true,
	sold:true,
	closed:true,
	rewards:true
}

let formData = [
	{
		title:"Client Name",
		data:["customer","name"],
		inputType:"TextInput"
	},
	{
		title:"Client Street",
		data:["customer","street"],
		inputType:"TextInput"
	},
	{
		title:"Client City",
		data:["customer","city"],
		inputType:"TextInput"
	},
	{
		title:"Client Zip",
		data:["customer","zip"],
		inputType:"TextInput",
		type:"number"
	},
	{
		title:"Contact Phone",
		data:["customer","phone"],
		inputType:"TextInput"
	},
	{
		title:"Contact Email",
		data:["customer","email"],
		inputType:"TextInput"
	},
	{
		title:"Contact Email",
		data:["customer","email"],
		inputType:"TextInput"
	}
]

let fields = {
	/*projectname:"Project Name",
	ref:'Project Num',
	client:"Client Name",
	street:"Client Street",
	city:"City",
	zip:"Client Zip",
	phone:"Contact Phone",
	email:"Contact Email",
	
	
	stage:"Stage",
	status:"Status",
	date:"Date",
	time:"Time Ran",*/
	
	source:"Lead Source",
	lead:"Lead Generator",
	rewards:"Rewards",
	prstdate:"Presented On",
	prstvia:"Presented Via",
	bookprc:"Book Price"

	/*
	comp:"Company",
	finance:"Financed",
	saletype:"Sale Type",
	progress:"Progress",
	bookprc:"Book Price",
	cat:"Category",
	sold:"Sold?",
	closed:"Closed"
	*/
}
const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042'];
function formatter(data) {
	return `${(data.percent * 100).toFixed(0)}%`
}

function GetBidChartData(bids) {
	let data = {}
	for (let i = 0; i < bids.length; i++) {
		if (bids[i].sold == true) {
			if (data[bids[i].cat] == undefined) {
				data[bids[i].cat] = {
					amount:Number(bids[i].amount),
					count:1
				}
			} else {
				data[bids[i].cat].amount += Number(bids[i].amount)
				data[bids[i].cat].count += 1
			}
		}
	}
	return data
}

/**
 * Starter class for an application. Contains a set of containers with a MenuBar allowing you to swap between tabs.
 * MenuBar style can be customized, and containers can be added to the Container to add pages to your app
 * Clone and modify this file whenever you want to create a new app that can sit inside App.js or the dock.
 */
export class TrackerEditor extends Component {
    constructor(props) {
        super(props)

		this.state = {
			id:"",
			ref:"",
			projectname:"",
			client:"",
			street:"",
			city:"",
			zip:"",
			phone:"",
			email:"",
			stage:"",
			status:"",
			date:"",
			ref:"",
			unit:"",
			time:{text:"Select One", value:""},
			source:{text:"Select One", value:""},
			lead:{text:"Select One", value:""},
			rewards:false,
			prstdate:"",
			prstvia:{text:"Select One", value:""},
			bookprc:false,
			bid:{
				sold:false,
				cat:"",
				amount:"",
				category:{text:"Select one", value:null},
				progress:{text:"Select one", value:null},
				comp:{text:"VHC", value:"VHC"},
				saletype:{text:"Select one", value:null}
			},
			DropDownKeys:{
				source:LeadSourceList,
				time:TimeRanList,
				prstvia:PresentedViaList,
				cat:CategoryList,
				comp:CompanyList,
				lead:LeadSourceList,
				saletype:SaleTypeList,
				progress:ProgressList
			}
		}

		this.GetForm = this.GetForm.bind(this)
		this.SetProperty = SetProperty.bind(this)
		this.SetBidProperty = this.SetBidProperty.bind(this)
		this.GetBids = this.GetBids.bind(this)
		this.AddBid = this.AddBid.bind(this)
		this.GetTotal = this.GetTotal.bind(this)
		this.GetBidData = this.GetBidData.bind(this)
		this.GetTopCategory = this.GetTopCategory.bind(this)
		this.GetTopBid = this.GetTopBid.bind(this)
		this.GetUnsold = this.GetUnsold.bind(this)
    }

	componentDidMount() {
		if (this.props.data != undefined) {
			let state = Clone(this.state)
			for (let key in state) {
				if (this.props.data[key] != undefined) {
					if (this.state.DropDownKeys[key] != undefined) {
						if (this.props.data[key] !== "") {
							state[key] = {
								text: this.props.data[key],
								value: this.props.data[key]
							}
						}
					} else {
						state[key] = this.props.data[key]
					}
				}
			}
			this.setState({
				...state
			})
		}

		this.setState({
			DropDownKeys:SetDroplists(this.props.settings)
		})
	}

	GetTotal() {
		let count = 0;
		for (let i = 0; i < this.props.data.bids.length; i++) {
			if (this.props.data.bids[i].sold == true) {
				count = count + Number(this.props.data.bids[i].amount)
			}
		}
		this.props.data.amount = count;
		return count
	}

	SetBidProperty(value, key) {
		let bid = Clone(this.state.bid)
		bid[key] = value
		this.setState({
			bid:bid
		})
	}

	AddBid() {
		let newBid = {
			amount:this.state.bid.amount,
			bookprice:true,
			cat:this.state.bid.category.value,
			comp:this.state.bid.comp.value,
			finance:"",
			progress:this.state.bid.progress.value,
			saletype:this.state.bid.saletype.value,
			sold:this.state.bid.sold,
		}
		this.props.AddBid(this.props.data._id, newBid)
	}

	GetBids() {
		let bids = []
		for (let i = 0; i < this.props.data.bids.length; i++) {
			bids.push(<div className='tablerow bid-tablerow'>
				<div className='row-item'>{this.props.data.bids[i].comp}</div>
				<DropDown 
					selected = {{text:this.props.data.bids[i].cat, value:this.props.data.bids[i].cat}}
					list = {CategoryList}
					ChangeFunction = {this.props.EditBid}
					data = {{_id:this.props.data._id, key:"cat", index:i}}
				/>
				<DropDown 
					selected = {{text:this.props.data.bids[i].saletype, value:this.props.data.bids[i].saletype}}
					list = {SaleTypeList}
					ChangeFunction = {this.props.EditBid}
					data = {{_id:this.props.data._id, key:"saletype", index:i}}
				/>
				<TextInput 
					value = {this.props.data.bids[i].amount}
					ChangeFunction = {this.props.EditBid}
					data = {{_id:this.props.data._id, key:"amount", index:i}}
					type = "number"
				/>
				<DropDown 
					selected = {{text:this.props.data.bids[i].progress, value:this.props.data.bids[i].progress}}
					list = {ProgressList}
					ChangeFunction = {this.props.EditBid}
					data = {{_id:this.props.data._id, key:"progress", index:i}}
				/>
				<Checkbox 
					value = {this.props.data.bids[i].sold}
					ClickFunction = {this.props.EditBid}
					data = {{_id:this.props.data._id, key:"sold", index:i}}
				/>
				<ImageButton 
					src = "https://www.vhpportal.com/repo/assets/icons/trash.png"
					ClickFunction = {this.props.RemoveBid}
					data = {{
						_id:this.props.data._id,
						index:i
					}}
				/>
			</div>)
		}
		return bids
	}

	GetForm() {
		let formdata = [];
		let noedit=[
			'projectname',
			'ref',
			'client',
			'street',
			'city',
			'zip',
			'stage',
			'status',
			'phone',
			'email',
			'date',
			'amount'
		]
		let aredates=[
			'prstdate'
		]
		let arenum=[
		]
		for (let key in fields) {
			let data = {
				title:fields[key],
				data:key,
				ChangeFunction:this.SetProperty
			}
			if(noedit.includes(key)){
				data.value = this.state[key];
			}else if(arenum.includes(key)){
				data.inputType = "TextInput";
				data.type="number";
				data.value = this.state[key];
			}else if(aredates.includes(key)){
				data.inputType = "TextInput";
				data.type="date";
				data.value = this.state[key];
			}else if (this.state.DropDownKeys[key] != undefined) {
				data.inputType = "DropDown"
				data.list = this.state.DropDownKeys[key]
				data.selected = this.state[key]
				data.searchable = true
			} else if (CheckboxKeys[key] != undefined) {
				data.inputType = "Checkbox"
				data.value = this.state[key]
				data.ClickFunction = this.SetProperty
			} else {
				data.inputType = "TextInput"
				data.value = this.state[key]
			}
			formdata.push(data)
		}
		//Loop through and grab values from state
		return <InputForm 
			formdata = {[
				...formdata
			]}
		/>
	}

	GetBidData() {
		///Combine by category
		let data = GetBidChartData(this.props.data.bids)
		let retval = []
		for (let key in data) {
			retval.push({
				amount:data[key].amount,
				cat:key
			})
		}

		return retval
	}

	GetTopCategory() {
		let data = GetBidChartData(this.props.data.bids)
		let top = null
		for (let key in data) {
			if (top == null) {
				top = key
			} else {
				if (data[top].count <= data[key].count) {
					top = key
				}
			}
		}

		return top
	}

	GetUnsold() {
		let count = 0;
		for (let i = 0; i < this.props.data.bids.length; i++) {
			if (this.props.data.bids[i].sold == false) {
				count += Number(this.props.data.bids[i].amount)
			}
		}

		return count
	}

	GetTopBid() {
		let data = GetBidChartData(this.props.data.bids)
		let top = null
		for (let key in data) {
			if (top == null) {
				top = key
			} else {
				if (data[top].amount <= data[key].amount) {
					top = key
				}
			}
		}

		return data[top].amount
	}

    render() {
        return(
            <>
				<Card
					title = {"Lead Overview - " + this.state.projectname + " - " + this.state.ref}
					id = "lead-overview-card"
					titlebar = {true}
					actions = {{minimize:null, close:this.props.CloseFunction}}
					data = {{minimize:null, close:this.props.closeData}}
				>
					<CardContent id = "lead-form-cont">
						<div className='lead-basic-info'>
							<BasicHeader 
								text = {this.state.projectname}
								headerClass = "left-align"
								id = "editor-project-name"
							/>
							<FormItem 
								data = {{
									title:"Stage:",
									value:this.state.stage,
									formItemClass:"editor-stage"
								}}
							/>
							<div id = "editor-status">{this.state.status}</div>
							<div id = "editor-contact-info">
								<div id = "editor-name">{this.state.client}</div>
								<div id = "editor-phone">{this.state.phone||"No phone provided"}</div>
								<div id = "editor-email">{this.state.email||"No email provided"}</div>
								<div id = "editor-address">{
									this.state.street + 
									" " + this.state.unit
								}</div>
								<div id = "editor-cityzip">{
									this.state.city +
									", " + this.state.zip
								}</div>
							</div>
						</div>
						<div className='lead-edit-form'>
							{this.GetForm()}
						</div>
						<div className='lead-bid-chart'>
							<div className='bid-summary-info'>
								<FormItem 
									data = {{
										title:"Total Bids",
										value:this.GetTotal() + this.GetUnsold(),
										formItemClass:"dollar-sign"
									}}
								/>
								<FormItem 
									data = {{
										title:"Amount Sold",
										value:this.GetTotal(),
										formItemClass:"dollar-sign"
									}}
								/>
								<FormItem 
									data = {{
										title:"Amount Unsold",
										value:this.GetUnsold(),
										formItemClass:"dollar-sign"
									}}
								/>
							</div>
							<ResponsiveContainer width="100%" height="100%">
								<PieChart>
									<Pie 
										data = {this.GetBidData()}
										dataKey="amount"
										nameKey="cat"
										cx="50%"
										cy="50%"
										outerRadius={65}
										innerRadius={40}
										fill='#8884d8'
										title = "Bids"
										label = {formatter}
									>
										{this.GetBidData().map((entry, index) => (
											<Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
										))}
									</Pie>
									<Legend 
										align = "left"
										layout='vertical'
									/>
									<Tooltip />
								</PieChart>
							</ResponsiveContainer>
						</div>
					</CardContent>
					<CardContent cardContentClass = "bid-cont no-padding">
						<CardContent cardContentClass = "bid-table-cont no-padding">
							<div className='action-row add-bid-row left'>
								<InputForm 
									formdata = {[
										{
											selected:this.state.bid.comp,
											list:CompanyList,
											data:"comp",
											ChangeFunction:this.SetBidProperty,
											inputType:"DropDown",
											id:"bid-dept-form-item"
										},
										{
											selected:this.state.bid.category,
											list:CategoryList,
											data:"category",
											ChangeFunction:this.SetBidProperty,
											inputType:"DropDown",
											id:"bid-cat-form-item"
										},
										{
											selected:this.state.bid.saletype,
											list:SaleTypeList,
											data:"saletype",
											ChangeFunction:this.SetBidProperty,
											inputType:"DropDown",
											id:"bid-cat-form-item"
										},
										{
											value:this.state.bid.amount,
											data:"amount",
											ChangeFunction:this.SetBidProperty,
											inputType:"TextInput",
											type:"number",
											id:"bid-amount-form-item"
										},
										{
											selected:this.state.bid.progress,
											list:ProgressList,
											data:"progress",
											ChangeFunction:this.SetBidProperty,
											inputType:"DropDown",
											id:"bid-cat-form-item"
										},
										{
											value:this.state.bid.sold,
											data:"sold",
											ClickFunction:this.SetBidProperty,
											inputType:"Checkbox",
											id:"bid-sold-form-item"
										}
									]}
								/>
								<ImageButton 
									ClickFunction = {this.AddBid}
									src = "https://www.vhpportal.com/repo/assets/icons/add.png"
									buttonClass = "icon-button"
								/>
							</div>
							<div className='tablerow header' id = "bid-table-header">
								<div className='row-item'>Dept</div>
								<div className='row-item'>Category</div>
								<div className='row-item'>Sale Type</div>
								<div className='row-item'>Amount</div>
								<div className='row-item'>Progress</div>
								<div className='row-item'>Sold</div>
							</div>
							<CardContent cardContentClass = "bid-table no-padding">
								{this.GetBids()}
							</CardContent>
						</CardContent>
					</CardContent>
					<div className='action-row right'>
						<TextButton 
							text = "Update"
							ClickFunction = {this.props.UpdateFunction}
							data = {this.state}
						/>
					</div>
				</Card>
			</>
        );
    }
}