import React, { Component } from 'react';
import { Clone, FloatContainer, BasicHeader, AppContainer, MenuSideBar, GetMenuCollapsed, SetCollapsibleTab } from 'vhp-component-library';
import { Core } from 'vhp-api';
import { TrackerView } from './Tracker/TrackerView';
import { AnalyticsView } from './Analytics/AnalyticsView';
import { TrackerEditor } from './TrackerEditor';
import { DropNote } from '../Components/VHP/DropNote/DropNote';


/**
{
	id:"CommissionView",
	title:"Commissions View"
},
 */
let apps = [
	{
		id:"TrackerView",
		title:"Leads"
	},
	{
		id:"AnalyticsView",
		title:"Analytics View"
	}
]

if (window.ipcRenderer != undefined) {
	ipcRenderer.on('page-close',(eve,data)=>{ipcRenderer.invoke('home',{})})
}

/**
 * @todo Move analytics here so all components can access it
 * @todo Move month track here so all components can access it
 */
export class ResidentialTracker extends Component {
    constructor(props) {
        super(props)

        this.state = {
            currentTab:0,
			quotes:[],
			leadOverviewActive:false,
			selectedLead:null,
			collapsed:"",
			settings:{},
			DropNoteProps:{
                showNote: false,
                text: 'Note',
                level: 'green',
                timeout: 0
            }                                                              
			//metrics:null
        }
        
		this.GetPage = this.GetPage.bind(this)
		this.ToggleLeadOverview = this.ToggleLeadOverview.bind(this)

		this.GetTrackers = this.GetTrackers.bind(this)
		this.UpdateTracker = this.UpdateTracker.bind(this)
		this.AddBid = this.AddBid.bind(this)
		this.EditBid = this.EditBid.bind(this)
		this.RemoveBid = this.RemoveBid.bind(this)
		this.GetMenuCollapsed = GetMenuCollapsed.bind(this)
		this.SetTab = SetCollapsibleTab.bind(this)
		this.GoHome = this.GoHome.bind(this)
		this.CloseNote = this.CloseNote.bind(this)

		//this.SetMetrics = this.SetMetrics.bind(this)
    }

	componentDidMount() {
		//Set up API
		if (this.props.settings.dev == false) {
			this.setState({
				Mongo: new Core({
					auth:{user:"VOGCH", pswrd:"vogel123"},
					sync:false, dev:{comments:true}
				})
			},()=>{
				this.state.Mongo.SENDrequest({
					pack:{
						db:'Replacement',
						collect:'Setting',
						method:"QUERY",
						options:{
							query:{}
						}
					},
					route:'STORE',
					request:'MART'
				}).then(answer=>{
					this.setState({
						settings:answer.result[0]
					},()=>{
						this.GetTrackers()
					})
				})
			})
			
		}
	}

	/**
     * Closes a note by modifying its showNote and timeout state.
     */
    CloseNote(){
        this.setState(state => ({
            DropNoteProps:{
                showNote:false,
                timeout:0
            }
        }));
    }

	GoHome() {
		if (window.ipcRenderer != undefined) {
			window.ipcRenderer.invoke('home',{});
		} else {
			window.open("https://www.vhpportal.com/")
		}
	}

	AddBid(id, bid) {
		let quotes = Clone(this.state.quotes)
		for (let i = 0; i < quotes.length; i++) {
			if (quotes[i]._id == id) {
				quotes[i].bids.push(bid)
				break;
			}
		}

		this.setState({
			quotes:quotes
		})
	}

	RemoveBid(data) {
		let quotes = Clone(this.state.quotes)
		for (let i = 0; i < quotes.length; i++) {
			if (quotes[i]._id == data._id) {
				quotes[i].bids.splice(data.index, 1)
				break;
			}
		}

		this.setState({
			quotes:quotes
		})
	}

	EditBid(value, data) {
		console.log(value, data)
		let quotes = Clone(this.state.quotes)
		for (let i = 0; i < quotes.length; i++) {
			if (quotes[i]._id == data._id) {
				if (data.key == "sold") {
					quotes[i].bids[data.index].sold = !quotes[i].bids[data.index].sold
				} else if (data.key == "cat"|| data.key == "saletype"|| data.key == "progress") {
					quotes[i].bids[data.index].cat = value.value
				} else {
					quotes[i].bids[data.index][data.key] = value
				}
			}
		}

		this.setState({
			quotes:quotes
		})
	}

	GetTrackers() {
		this.setState({
			DropNoteProps:{
				showNote:true,
				level:"blue",
				text:"Loading data",
				timeout:1000
			}
		})
		if (this.props.user.name == "RESMAN") {
			this.state.Mongo.SENDrequest({
				pack:{
					db:'Replacement',
					collect:'Tracking350',
					method:"QUERY",
					options:{
						query:{}
					}
				},
				route:'STORE',
				request:'MART'
			}).then(answer=>{
				if (answer.success) {
					this.setState({
						quotes:answer.result,
						DropNoteProps:{
							showNote:true,
							level:"green",
							text:"Data loaded",
							timeout:2000
						}
					})
				} else {
					this.setState({
						DropNoteProps:{
							showNote:true,
							level:"red",
							text:answer.msg,
							timeout:0
						}
					})
				}
			})
		} else {
			this.state.Mongo.SENDrequest({
				pack:{
					db:'Replacement',
					collect:'Tracking350',
					method:"QUERY",
					options:{
						query:{estimator:this.props.user.name}
					}
				},
				route:'STORE',
				request:'MART'
			}).then(answer=>{
				if (answer.success) {
					this.setState({
						quotes:answer.result,
						DropNoteProps:{
							showNote:true,
							level:"green",
							text:"Data loaded",
							timeout:10000
						}
					})
				} else {
					this.setState({
						DropNoteProps:{
							showNote:true,
							level:"red",
							text:answer.msg,
							timeout:0
						}
					})
				}
			})
		}
	}

	ToggleLeadOverview(data) {
		if (data != undefined) {
			for (let i = 0; i < this.state.quotes.length; i++) {
				if (this.state.quotes[i]._id == data._id) {
					this.setState({
						leadOverviewActive:!this.state.leadOverviewActive,
						selectedLead:i
					})
					break;
				}
			}
		} else {
			this.setState({
				leadOverviewActive:!this.state.leadOverviewActive,
				selectedLead:-1
			})
		}
	}

	UpdateTracker(data) {
		let quotes = Clone(this.state.quotes)
		for (let key in data) {
			if (quotes[this.state.selectedLead][key] != undefined) {
				if (data[key].text != undefined) {
					quotes[this.state.selectedLead][key] = data[key].value
				} else {
					quotes[this.state.selectedLead][key] = data[key]
				}
			}
		}
		if (this.props.settings.dev == true) {
			this.setState({
				leadOverviewActive:!this.state.leadOverviewActive,
				quotes:quotes
			})
		} else {
			//if(this.state.quotes[this.state.selectedLead].trackid){
			this.state.Mongo.SENDrequest({
				pack:{
					db:'Replacement',
					collect:'Tracking350',
					method:"UPDATE",
					options:{
						query:{id:this.state.quotes[this.state.selectedLead].id},
						update:{...quotes[this.state.selectedLead]}
					}
				},
				route:'STORE',
				request:'MART'
			}).then(answer=>{
				//console.log("MONGO UPDATE RESPONSE	", answer)
				this.setState({
					DropNoteProps:{
						showNote:true,
						level:"green",
						text:"Tracker updated",
						timeout:0
					}
				},()=>{
					this.GetTrackers()
				})
			})
		}
	}

	GetPage() {
		if (this.state.currentTab == 0) {
			return (
				<TrackerView 
					settings = {this.state.settings.data}
					quotes = {this.state.quotes}
					ToggleLeadOverview = {this.ToggleLeadOverview}
					//SetMetrics = {this.SetMetrics}
				/>
			)
		} else {
			return (
				<AnalyticsView 
					settings = {this.state.settings.data}
					quotes = {this.state.quotes}
					//metrics = {this.state.metrics}
				/>
			)
		}
	}

    render() {
        return(
			<>
				<MenuSideBar 
                    tabs = {["Leads","Analytics"]}
                    SetTab = {this.SetTab}
                    MenuStyle = {this.state.collapsed}
					images = {[
						"https://www.vhpportal.com/repo/assets/icons/calendar.png",
                        "https://www.vhpportal.com/repo/assets/icons/data-analytics.png",
                    ]}
					CollapseFunction = {this.GetMenuCollapsed}
					bottomActions = {[
						{
							text:"Home",
							src:"https://www.vhpportal.com/repo/assets/icons/home2.png",
							type:"ImageButton",
							keyName:"home",
							ClickFunction:this.GoHome
						}
					]}
					moreActions = {[
						{
							src:"https://www.vhpportal.com/repo/assets/icons/logout.png",
							type:"ImageButton",
							keyName:"home",
							ClickFunction:this.props.LogUserOut
						}
					]}
                />
				<AppContainer id = {this.state.collapsed} containerClass = "side-bar-container">
					{this.GetPage()}
				</AppContainer>
				<DropNote 
					{...this.state.DropNoteProps}
					CloseNote = {this.CloseNote}
				/>
				{this.state.leadOverviewActive&&<FloatContainer>
					<TrackerEditor
						CloseFunction = {this.ToggleLeadOverview}
						data = {this.state.quotes[this.state.selectedLead]}
						UpdateFunction = {this.UpdateTracker}
						AddBid = {this.AddBid}
						RemoveBid = {this.RemoveBid}
						EditBid = {this.EditBid}
						settings = {this.state.settings.data}
					/>
				</FloatContainer>}
			</>
        );
    }
}