import React, { Component } from 'react';
import { CardContent, Card, TextInput, FormItem, DataTable, SetProperty } from 'vhp-component-library';

import '../Styles/TrackerSearchCard.css'

import { dateformat } from '../../bin/vg-gentools';

function Search(object, search, fields) {
	if (search == "") {
		return true
	} else {
		let retval = false;
		for (let key in fields) {
			if (object[key] != undefined && fields[key] == true) {
				if (String(object[key]).toLowerCase().includes(search.toLowerCase())) {
					retval = true
				}
			}
		}

		return retval
	}
}

export class TrackerSearchCard extends Component {
    constructor(props) {
        super(props)

        this.state = {
            status:true,
			stage:true,
			projectname:true,
			client:true,
			search:this.props.search||""
        }

		this.GetTable = this.GetTable.bind(this)
		this.SetProperty = SetProperty.bind(this)
    }

	GetTable() {
		let data = []

		for (let i = 0; i < this.props.quotes.length; i++) {
			if (Search(this.props.quotes[i], this.state.search, {
				status:this.state.status,
				stage:this.state.stage,
				projectname:this.state.projectname,
				client:this.state.client
			})) {
				data.push(this.props.quotes[i])
			}
		}

		let table = []
		for (let i = 0; i < data.length; i++) {
			const onclick = () => {
				this.props.ToggleLeadOverview(data[i])
			}
			table.push(<div className='tablerow' onClick={onclick}>
				<div className='row-item'>{data[i].projectname}</div>
				<div className='row-item'>{data[i].estimator}</div>
				<div className='row-item'>{dateformat(data[i].date)}</div>
				<div className='row-item'>{data[i].stage}</div>
				<div className='row-item'>{data[i].status}</div>
				<div className='row-item'>{data[i].client}</div>
			</div>)
		}

		return <CardContent cardContentClass = "data-table">
			{table}
		</CardContent>
	}

    render() {
        return(
            <Card
				id = "tracker-search-card"
				titlebar = {true}
				title = "Search Trackers"
				actions = {{minimize:null, close:this.props.CloseFunction}}
				data = {{minimize:null, close:this.props.closeData}}
			>
				<div className='action-row center' id = "tracker-search-actions">
					<FormItem
						data = {{
							title:"Search:",
							inputType:"TextInput",
							value:this.state.search,
							ChangeFunction:this.SetProperty,
							data:"search"
						}}
					/>
					<FormItem
						data = {{
							title:"Status:",
							inputType:"Checkbox",
							value:this.state.status,
							ClickFunction:this.SetProperty,
							data:"status"
						}}
					/>
					<FormItem
						data = {{
							title:"Stage:",
							inputType:"Checkbox",
							value:this.state.stage,
							ClickFunction:this.SetProperty,
							data:"stage"
						}}
					/>
					<FormItem
						data = {{
							title:"Project Name:",
							inputType:"Checkbox",
							value:this.state.projectname,
							ClickFunction:this.SetProperty,
							data:"projectname"
						}}
					/>
					<FormItem
						data = {{
							title:"Client:",
							inputType:"Checkbox",
							value:this.state.client,
							ClickFunction:this.SetProperty,
							data:"client"
						}}
					/>
				</div>
				<CardContent id = "tracker-search-card-table">
					{this.GetTable()}
				</CardContent>
			</Card>
        );
    }
}