import React, { Component } from 'react';
import { MenuTabBar, Card, DataTable, Months, TextButton, BasicHeader, CardContent } from 'vhp-component-library';

let tableData = [
	"CO",
	"01",
	"02",
	"03",
	"04",
	"05",
	"06",
	"07",
	"08",
	"09",
	"10",
	"11",
	"12"
]

import '../Styles/TrackerMonthView.css'
import { SORTlist, dateformat } from '../../bin/vg-gentools';

/**
 * @todo Logic for generating the month and 
 */
export class TrackerMonthView extends Component {
    constructor(props) {
        super(props)

        this.state = {
            currentTab:0,
			filter:"projectname",
			order:1
        }
        
        this.SetTab = this.SetTab.bind(this)
		this.GetTable = this.GetTable.bind(this)
		this.GetHeaders = this.GetHeaders.bind(this)
		this.ToggleSearch = this.ToggleSearch.bind(this)
    }

	componentDidMount() {
		this.SetTab(new Date().getMonth() + 1)
	}

	SetTab(TabID) {
		console.log(TabID, Months[TabID-1])
		this.setState({
			currentTab:TabID
		},()=>{
			if (TabID == 0) {
				this.props.SetMonth({text:Months[0].text,value:1})
			} else {
				this.props.SetMonth({
					text:Months[TabID-1].text,
					value:TabID
				})
			}
		})
	}

	GetHeaders() {
		/*{
			"projectname":"Project Name",
			"estimator":"Consultant",
			"date":"Date",
			"stage":"Stage",
			"status":"Status",
			"client":"Client ",
		}*/
		return <div className='tablerow header show'>
			<TextButton 
				buttonClass = "row-item"
				text = "Project Name"
				ClickFunction = {this.ToggleSearch}
				data = "projectname"
			/>
			<TextButton 
				buttonClass = "row-item"
				text = "Consultant"
				ClickFunction = {this.ToggleSearch}
				data = "estimator"
			/>
			<TextButton 
				buttonClass = "row-item"
				text = "Date"
				ClickFunction = {this.ToggleSearch}
				data = "date"
			/>
			<TextButton 
				buttonClass = "row-item"
				text = "Stage"
				ClickFunction = {this.ToggleSearch}
				data = "stage"
			/>
			<TextButton 
				buttonClass = "row-item"
				text = "Status"
				ClickFunction = {this.ToggleSearch}
				data = "status"
			/>
			<TextButton 
				buttonClass = "row-item"
				text = "Client"
				ClickFunction = {this.ToggleSearch}
				data = "client"
			/>
		</div>
	}

	ToggleSearch(filter) {
		let order = 1
		if (this.state.order == -1) {
			order = 1
		} else {
			order = -1
		}

		this.setState({
			order:order,
			filter:filter
		})
	}
 
	/**
	 * Loop through quotes and return only quotes that are part of the selected month.
	 * @todo Filter CO based on reqs
	 */
	GetTable() {
		//Determine if it's CO or a month
		let data = []
		let lastYear = new Date(new Date().setFullYear(new Date().getFullYear() - 1));
		lastYear = lastYear.getFullYear()
		let thisYear = new Date(new Date().setFullYear(new Date().getFullYear() - 0));
		thisYear = thisYear.getFullYear()
		for (let i = 0; i < this.props.quotes.length; i++) {	
			if (this.state.currentTab == 0) {
				//Return carryover		
				if (this.props.quotes[i].date.slice(0, 4) != thisYear) {
					data.push(this.props.quotes[i])
				}
			} else {
				if (this.props.quotes[i].date.slice(0, 4) == thisYear) {
					if (this.props.quotes[i].date.slice(5,7) == tableData[this.state.currentTab]) {
						data.push(this.props.quotes[i])
					}
				}
			}
		}

		data = SORTlist(data, this.state.filter,this.state.order)

		let table = []
		for (let i = 0; i < data.length; i++) {
			const onclick = () => {
				this.props.ToggleLeadOverview(data[i])
			}
			table.push(<div className='tablerow' onClick={onclick}>
				<div className='row-item'>{data[i].projectname}</div>
				<div className='row-item'>{data[i].estimator}</div>
				<div className='row-item'>{dateformat(data[i].date)}</div>
				<div className='row-item'>{data[i].stage}</div>
				<div className='row-item'>{data[i].status}</div>
				<div className='row-item'>{data[i].client}</div>
			</div>)
		}
		
		/*return <DataTable 
			data = {data}
			headers = {{
				"projectname":"Project Name",
				"estimator":"Consultant",
				"date":"Date",
				"stage":"Stage",
				"status":"Status",
				"client":"Client ",
			}}
			ClickFunction = {this.props.ToggleLeadOverview}
		/>*/
		return <CardContent cardContentClass = "data-table">
			{table}
		</CardContent>
	}

    render() {
        return(
            <>
				<Card 
					cardClass = "no-padding outline-card"
					id = "tracker-month-view"
					title = "Lead Tracker"
					titlebar = {true}
				>
					<MenuTabBar 
						tabs = {["CO", "Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"]}
						SetTab = {this.SetTab}
						MenuStyle = "top-bar"
						default = {new Date().getMonth() + 1}
					/>
					{this.GetHeaders()}
					{this.GetTable()}
				</Card>
            </>
        );
    }
}